import './Login.css'
import { useState } from 'react'
// Icons 
import visibiliy from './Icons/Login_visibilityicon.svg'
import remember from './Icons/Login_remembericon.svg'
import googleicon from './Icons/Login_googleicon.svg'
const Login = () => {
    const [loginarea, setLoginarea] = useState(true)
    return ( 
        <div className="Login-content">
               {
                loginarea ?(
                    <div className="Login-items">
                         <div className="Login-items-button">
                    <button type="submit" className='Login-items-button-Login' onClick={() => setLoginarea(true)}></button>
                    <button type="submit" className='Login-items-button-Singup' onClick={() => setLoginarea(false)}> </button>
                    </div>
                    <div className="Login-items-inputs">
                        <label htmlFor=""> </label>
                        <input type="text" name="" id="" placeholder='      '/>
                        <label htmlFor=""> </label>
                        <input type="text" name="" id="" /> <img src={visibiliy} alt="visibility icon" width="24px" height="24px"/>
                    </div>
                    <div className="Login-items-b-submit">
                        <button type="submit">  </button>
                    </div>
                    <div className="Login-items-faramoshi">
                        <span>  </span>
                        <span className='Login-items-faramoshi-remember'><img src={remember} alt="remember icon" />   </span>
                        <span className='Login-items-faramoshi-last'>     </span>
                    </div>
                    <hr />
                    <div className="Login-items-google">
                        <button type="submit">  <img src={googleicon} alt="google icon" /></button>
                    </div>
                    </div>
                )
                : (
                    <div className="Login-items">
                         <div className="Login-items-button">
                        <button type="submit" className='Login-items-button-Login2' onClick={() => setLoginarea(true)}></button>
                        <button type="submit" className='Login-items-button-Singup2' onClick={() => setLoginarea(false)}> </button>
                    </div>
                    <div className="Login-items-inputs-singup">
                        <label htmlFor=""> </label>
                        <input type="text" name="" id="" placeholder='+ | '/>
                    </div>
                    <div className="Login-items-b-submit2">
                        <button type="submit">  </button>
                    </div>
                    </div>
                )
               }
        </div>
     );
}
 
export default Login;