import './Profile.css'
import { useState } from 'react';
// Images
import profilepic1 from './image/profilepic1.png'
import profile_companie1 from './image/prof1.png'
import profile_companie2 from './image/prof2.png'
import profile_companie3 from './image/prof3.jpg'
import profile_companie4 from './image/prof4.png'
import profile_companie5 from './image/prof5.jpg'
// Icons
import profile_edit from './Icons/Profile_edit.svg'
import profile_bookmark_selected from './Icons/Profile_bookmark_selected.svg'
import profile_bookmark from './Icons/Profile_bookmark.svg'
import bookmarked from './Icons/bookmarksaved_blue.svg'
import showpassicon from './Icons/Login_visibilityicon.svg'
import profile_sabtagahiicon1 from './Icons/Profile_sabtagahi1.svg'
import profile_sabtagahiicon2 from './Icons/profile_sabtagahi2.svg'
import profile_rezomeiconblue from './Icons/Profile_rezomeicon.svg'
import profile_rezomeiconwhite from './Icons/Profile_rezomewhite.svg'
const Profile = () => {
    const [activespan, setActivespan] = useState(0);
    const [picchange, setPicchange] = useState(false);
    const [showpass1, setShowpass1] = useState(false);
    const [showpass2, setShowpass2] = useState(false);
    const [showpass3, setShowpass3] = useState(false);
    const [agahiheader, setAgahiheader] = useState(0);

    // see the password function 
    const show1 = () => {
        setShowpass1(!showpass1);
      };
    const show2 = () => {
        setShowpass2(!showpass2);
      };
    const show3 = () => {
        setShowpass3(!showpass3);
      };
    // bookmark cards   
    const Bookmarkcard = (props)=>{
        return (
                    <div className="Profile-bookmark-box">
                        <div className="Profile-bookmark-1">
                            <img src={props.img} alt="companie logo" width="55px" height="55px"/>
                        </div>
                        <div className="Profile-bookmark-2">
                            <h1>{props.companiename}</h1>
                            <h2>{props.companiedes}</h2>
                            <span>{props.companieprice}</span>
                            <h3>{props.companiedate}</h3>     
                        </div>
                        <div className="Profile-bookmark-3">
                            <img src={bookmarked} alt="bookmark icon" width="24px" height="24px"/>
                        </div>
                    </div>
        )
    }
    // agahi cards 
    const Agahicard = (props)=>{
        return(
            <>
                <div className="Profile-agahi-content-card">
                    <div className="Profile-agahi-content-card-cloum1 Profile-agahi-content-card-cloum">
                        {
                            agahiheader === 2 ? (<>
                                <button className='Profile-agahi-content-card-cloum1-btn3'> </button>
                            
                            </>): agahiheader === 1 ? (<>
                                <button className='Profile-agahi-content-card-cloum1-btn4'>  </button>
                            </>):(<>      
                                <button className='Profile-agahi-content-card-cloum1-btn1'> </button>
                                <button className='Profile-agahi-content-card-cloum1-btn2'> </button>
                            </>)
                        }
                    </div>
                    <div className="Profile-agahi-content-card-cloum2 Profile-agahi-content-card-cloum">
                        <h1>{props.price}</h1>
                    </div>
                    <div className="Profile-agahi-content-card-cloum3 Profile-agahi-content-card-cloum">
                        <h1>{props.header} </h1>
                        <h2>{props.des}</h2>
                    </div>
                    <div className="Profile-agahi-content-card-cloum4 Profile-agahi-content-card-cloum">
                        <img src={props.img} alt="companie logo" width="55px" height="55px"/>
                    </div>
                </div>
            </>
        )
    }
    return ( 
        <div className="Profile-split">
            {
                picchange === true ? (
                    <>

                    </>
                ) : null
            }
            <div className="Profile-content">
                {
                    activespan === 0 ? (
                        <>
                             <div className="Profile-content-sex">
                    <h1></h1>
                    <div className="Profile-content-sex-b">
                        <button type="submit"></button>
                        <button type="submit"></button>
                    </div>
                </div>
                <div className="Profile-content-other">
                    <div className="Profile-content-pic">
                        <img src={profilepic1} alt="profile picture" width="88px" height="88px"/>
                        <div className="Profile-content-pic-text">
                            <h1> </h1>
                            <span></span>
                        </div>
                    </div>
                    <div className="Profile-content-row">
                        <label for="profile-name"></label>
                        <label for="profile-family"></label>
                    </div>
                    <div className="Profile-content-row">
                        <input type="text" name="" id="profile-name" required/>
                        <input type="text" name="" id="profile-family" required/>
                    </div>
                    {/* next row */}
                    <div className="Profile-content-row">
                        <label for="profile-phone"> </label>
                        <label for="profile-email"></label>
                    </div>
                    <div className="Profile-content-row">
                        <input type="text" name="" id="profile-phone" required/>
                        <input type="email" name="" id="profile-email" required/>
                    </div>
                    {/* next row */}
                    <div className="Profile-content-row">
                        <label for="profile-city"></label>
                        <label for="profile-address"></label>
                    </div>
                    <div className="Profile-content-row">
                        <select name="" id="profile-city" required>
                            <option value="default"></option>
                            <option value="tehran"></option>
                            <option value="mashhad"></option>
                            <option value="isfahan"></option>
                            <option value="karaj"></option>
                            <option value="tabriz"></option>
                            <option value="shiraz"></option>
                            <option value="qom"></option>
                            <option value="ahvaz"></option>
                            <option value="kermanshah"></option>
                            <option value="urmia"></option>
                            <option value="rasht"></option>
                            <option value="zahedan"></option>
                            <option value="hamedan"></option>
                            <option value="kerman"></option>
                            <option value="yazd"></option>
                            <option value="ardabil"></option>
                            <option value="bandar_abbas"></option>
                            <option value="arak"></option>
                            <option value="eslamshahr"></option>
                            <option value="neyshabur"></option>
                            <option value="babol"></option>
                            <option value="amol"></option>
                            <option value="sari"></option>
                            <option value="qazvin"></option>
                            <option value="khorramabad"></option>
                            <option value="qarchak"></option>
                            <option value="borujerd"></option>
                            <option value="mahabad"></option>
                            <option value="sanandaj"></option>
                            <option value="gorgan"></option>
                            <option value="shahriar"></option>
                            <option value="varamin"></option>
                            <option value="bandar_anzali"> </option>
                            <option value="behshahr"></option>
                            <option value="zanjan"></option>
                            <option value="saveh"></option>
                            <option value="maragheh"></option>
                            <option value="semnan"></option>
                            <option value="shirvan"></option>
                            <option value="gonbad_kavus"></option>
                            <option value="andimeshk"></option>
                            <option value="abadan"></option>
                            <option value="dezful"></option>
                            <option value="masjed_soleyman"> </option>
                            <option value="shush"></option>
                            <option value="ilam"></option>
                            <option value="malayer"></option>
                            <option value="sarpol_zahab"> </option>
                            <option value="kangavar"></option>
                            <option value="farsan"></option>
                            <option value="lahijan"></option>
                            <option value="shahroud"></option>
                            <option value="mahabad"></option>
                            <option value="saghez"></option>
                            <option value="borazjan"></option>
                            <option value="khorramshahr"></option>
                            <option value="naqadeh"></option>
                            <option value="rudsar"></option>
                            <option value="sabzevar"></option>
                        </select>
                        <input type="email" name="" id="profile-address" required/>
                    </div>
                    <button type="button"> <img src={profile_edit} alt="edit profile icon" width="24px" height="24px"/></button>
                </div>
                        </>
                    ) : activespan === 1 ? (
                        <div className="Profile-bookmark">
                            <Bookmarkcard img={profile_companie1} companiename="  " companiedes="  " companieprice="    " companiedate="  "></Bookmarkcard>
                            <Bookmarkcard img={profile_companie2} companiename="   " companiedes="  " companieprice="    " companiedate="  "></Bookmarkcard>
                            <Bookmarkcard img={profile_companie3} companiename=" " companiedes=" " companieprice="    " companiedate="  "></Bookmarkcard>
                            <Bookmarkcard img={profile_companie4} companiename="  " companiedes=" " companieprice="    " companiedate="  "></Bookmarkcard>
                            <Bookmarkcard img={profile_companie5} companiename="   " companiedes="  " companieprice="    " companiedate="  "></Bookmarkcard>
                        </div>
                        
                        
                    ) : activespan === 2 ? (
                        <div className="Profile-changep">
                            <div className="Profile-changep-row">
                                <div className="Profile-changep-row-box">
                                    <label htmlFor="currentpass">  </label>
                                    <input type={showpass1 ? "text" : "password"} name="" id="currentpass" /><img src={showpassicon} alt="show password" width="24px" height="24px" onClick={show1}/>
                                </div>
                            </div>
                            <div className="Profile-changep-row">
                                <div className="Profile-changep-row-box2">
                                    <label htmlFor="currentpass">  </label>
                                    <input type={showpass2 ? "text" : "password"} name="" id="currentpass" /><img src={showpassicon} alt="show password" width="24px" height="24px" onClick={show2}/>
                                </div>
                                <div className="Profile-changep-row-box2">
                                    <label htmlFor="currentpass">   </label>
                                    <input type={showpass3 ? "text" : "password"} name="" id="currentpass" /><img src={showpassicon} alt="show password" width="24px" height="24px" onClick={show3}/>
                                </div>
                            </div>
                            <button type="submit"> </button>
                        </div>
                    ) : activespan === 3 ? (
                        <div className='Profile-agahi'>
                            <div className="Profile-agahi-headers">
                                <div className="Profile-agahi-headers-text">
                                    <h1 onClick={()=>setAgahiheader(0)} className={agahiheader === 0 ? 'Profile-agahi-headers-text-active' : ''}> </h1>
                                    <h1 onClick={()=>setAgahiheader(1)} className={agahiheader === 1 ? 'Profile-agahi-headers-text-active' : ''}>  </h1>
                                    <h1 onClick={()=>setAgahiheader(2)} className={agahiheader === 2 ? 'Profile-agahi-headers-text-active' : ''}>  </h1>
                                </div>                              
                            </div>
                            <div className="Profile-agahi-content">
                                    {
                                        agahiheader === 0 ? (
                                            <>
                                                <Agahicard img={profile_companie1} header="   " price="    " des="  "></Agahicard>
                                                <Agahicard img={profile_companie3} header="   " price="    " des="  "></Agahicard>                            
                                            </>
                                        ) : agahiheader === 1 ? (
                                            <>
                                                <Agahicard img={profile_companie1} header="   " price="    " des="  "></Agahicard>                          
                                            </>
                                        ) : agahiheader === 2 ? (
                                            <>
                                                <Agahicard img={profile_companie1} header="   " price="    " des="  "></Agahicard>
                                                <Agahicard img={profile_companie3} header="   " price="    " des="  "></Agahicard>                            
                                                <Agahicard img={profile_companie4} header="   " price="    " des="  "></Agahicard>                            
                                            </>
                                        ) : null
                                    }
                            </div>
                        </div>
                    ) : activespan === 4 ? (
                        <div className="Profile-sabtagahi">
                            <div className="Profile-sabtagahi-header">
                                <h1>           .</h1>
                                <p>                 .                               .</p>
                            </div>
                            <div className="Profile-sabtagahi-content">
                                <div className="Profile-sabtagahi-content-box">
                                    <div className="Profile-sabtagahi-content-box-header">
                                        <h1> </h1>
                                        <img src={profile_sabtagahiicon2} alt="icon" />
                                    </div>
                                    <div className="Profile-sabtagahi-content-box-texts">
                                        <p>                 .                               .</p>
                                    </div>
                                    <button type="submit">  </button>
                                </div>
                                <div className="Profile-sabtagahi-content-box">
                                    <div className="Profile-sabtagahi-content-box-header">
                                        <h1> </h1>
                                        <img src={profile_sabtagahiicon1} alt="icon" />
                                    </div>
                                    <div className="Profile-sabtagahi-content-box-texts">
                                        <p>                 .                               .</p>
                                    </div>
                                    <button type="submit">  </button>
                                </div>
                            </div>
                        </div>
                    ) : activespan === 5 ? (
                        <div className="Profile-eshterak">
                            <div className="Profile-eshterak-header">
                                <h1> </h1>
                                <p>                 .                               . </p>
                            </div>
                            <div className="Profile-eshterak-price">
                                <div className="Profile-eshterak-price-cloum">
                                    <h1>  </h1>
                                    <h2>  </h2>
                                    <h2>  </h2>
                                    <h2>  </h2>
                                    <h2>  </h2>
                                    <span>, </span>
                                    <button type="submit"> </button>
                                </div>
                                <div className="Profile-eshterak-price-cloum Profile-eshterak-price-cloum-special">
                                    <div className="Profile-eshterak-price-cloum-special-header">
                                        <h1>  </h1>
                                        <h2>( )</h2>
                                    </div>
                                    <h2>  </h2>
                                    <h2>  </h2>
                                    <h2>  </h2>
                                    <h2>  </h2>
                                    <span>, </span>
                                    <button type="submit"> </button>
                                </div>
                                <div className="Profile-eshterak-price-cloum">
                                    <h1>  </h1>
                                    <h2>  </h2>
                                    <h2>  </h2>
                                    <h2>  </h2>
                                    <h2>  </h2>
                                    <span>, </span>
                                    <button type="submit"> </button>
                                </div>
                            </div>
                        </div>
                    ) : (
                        <div className="Profile-rezome">
                            <h1> </h1>
                            <p>                  .</p>
                            <span>    </span>
                            <button> </button>
                        </div>
                    ) 
                }         
            </div>
            <div className="Profile-category">
                <h1></h1>
                <span onClick={()=>setActivespan(0)} className={activespan === 0 ? 'Profile-category-active-span' : ''}> </span>
                <span onClick={()=>setActivespan(1)} className={activespan === 1 ? 'Profile-category-active-span' : ''}><img src={activespan === 1 ? profile_bookmark_selected : profile_bookmark} alt="bookmark icon" width="24px" height="24px"/>  </span>
                <span onClick={()=>setActivespan(2)} className={activespan === 2 ? 'Profile-category-active-span' : ''}> </span>
                <span onClick={()=>setActivespan(3)} className={activespan === 3 ? 'Profile-category-active-span' : ''}> </span>
                <span onClick={()=>setActivespan(4)} className={activespan === 4 ? 'Profile-category-active-span' : ''}> </span>
                <span onClick={()=>setActivespan(5)} className={activespan === 5 ? 'Profile-category-active-span' : ''}></span>
                <span onClick={()=>setActivespan(6)} className={activespan === 6 ? 'Profile-category-active-span' : ''}><img src={activespan === 6 ? profile_rezomeiconblue : profile_rezomeiconwhite} alt="bookmark icon" width="24px" height="24px"/> </span>
                <h2>  </h2>
            </div>
        </div>
     );
}
 
export default Profile;